@import "../components/variables";

@media screen and (max-width: 1500px), screen and (max-device-width: 1500px) {
  body {
    .main {
      .container {
        .content__actualProduct,
        .content__saleProduct {
          &__slider{
            .slider {
              height: 325px;
              ul {
                li{
                  width: 200px;
                  height: 325px;
                  a{
                    img{
                      margin-top: 30px;
                      width: 175px;
                      height: 185px;
                    }
                  }
                  .hover{
                    width: 198px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1310px), screen and (max-device-width: 1310px) {
  body {
    .main {
      .container {
        .content__actualProduct,
        .content__saleProduct {
          &__slider{
            .slider {
              ul {
                li{
                  width: 225px;
                  a{
                    img{
                      margin-top: 15px;
                      width: 200px;
                      height: 200px;
                    }
                  }
                  .hover{
                    width: 223px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1150px), screen and (max-device-width: 1150px){
  body{
    .main{
      .container{
        .content__actualProduct,
        .content__saleProduct{
          &__headline{
            div{
              flex-direction: column;
              margin: 20px 40px 0 0;
              p{
                margin: 5px 0;
              }
            }
          }
          &__slider{
            .slider {
              height: 325px;
              ul {
                li{
                  width: 200px;
                  a{
                    img{
                      margin-top: 30px;
                      width: 175px;
                      height: 185px;
                    }
                  }
                  .hover{
                    width: 198px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 940px), screen and (max-device-width: 940px){
  body{
    .main{
      .container{
        .content__actualProduct,
        .content__saleProduct{
          &__headline{
            align-items: center;
            div{
              margin: 20px 0 0 0;
              p{
                font-size: 1em;
                line-height: 1.125em;
                span{
                  font-size: 1.125em;
                }
              }
            }
          }
          &__showMore{
            a{
              margin: 15px 0;
              width: 150px;
            }
          }
        }
      }
    }
  }
}


@media screen and (max-width: 870px), screen and (max-device-width: 870px){
  body{
    .main{
      .container{
        .content__actualProduct,
        .content__saleProduct{
          &__headline{
            h5{
              font-size: 2em;
              line-height: 2.125em;
            }
          }
          &__slider{
            img[alt*="img"]{
              display: none;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 680px), screen and (max-device-width: 680px){
  body{
    .slider{
      img{
        height: 360px;
      }
    }
    .main{
      .container{
        .content__actualProduct,
        .content__saleProduct{
          &__headline{
            flex-wrap: wrap;
            h5{
              width: 100%;
            }
            div{
              margin: 0;
              width: 100%;
              p{
                font-size: .75em;
                span{
                  width: 100px;
                }
              }
            }
          }
        }
      }
    }
  }
}

